<template>
    <div class="balance">
        <c-top-back></c-top-back>
        <r-messages :src="src" msg="" :money="money" class="account" :fivetext="fivetext">
            <div slot="list" class="slot">
                <r-group>
                    <r-cell title="" :value="txtStatus" :is-link="islink" :link="link"
                            :class="{flag:isShow}"></r-cell>
                    <r-cell title="" link="/tx/cashrecord" is-link></r-cell>
                </r-group>
            </div>
            <div slot="btn">
                <r-button type="primary" text="" @click.native="btnClick" :disabled="btnDisabled"></r-button>
            </div>
        </r-messages>
        <div class="hint">
            <h3>
                <r-icon type="info"></r-icon>
                
            </h3>
            1.3/
            <br/>2.0.6%
            <br/>3.=--
            <br/>4.1000
            <br/>5.1000-1000*0.6%-3=991
        </div>
    </div>

</template>
<script>
    import utils from '../../libs/utils.js'
    export default {
        data (){
            return {
                src: "data:image/jpeg;base64,",
                money: 0,
                desc: "24",
                btnDisabled: true,
                txtStatus: "",
                islink: true,
                link: "/tx/verifyInfo",
                accountInfo: {},
                isShow: false,
                fivetext: "24",
                bindCard: false,//
                rzStatus: "",
            }
        },
        mounted: function () {
            this.$nextTick(function () {
                let that = this;
                //
                utils.ajax({
                    url: "/seller/account/getAccountAmount",
                    success: function (res) {
                        if (res.code == "SUCCESS") {
                            let json = res.result;
                            that.money = utils.formatPrice(json.withdrawals_amount);
                        } else {
                            that.$vux.alert.show(res.message);
                        }
                    }
                })
                //
                utils.ajax({
                    url: "/seller/account/get",
                    success: function (res) {
                        if (res.code == "SUCCESS") {
                            if (res.result) {
                                that.txtStatus = "";
                                that.islink = false;
                                that.link = "";
                                that.accountInfo = res.result;
                                utils.ajax({
                                    url: "/seller/account/getStoreBankCard",
                                    success: function (response) {
                                        if (response.code == "SUCCESS") {
                                            that.isShow = true;
                                            that.txtStatus = response.result.card_no;
                                            that.bindCard = true;//
                                            if (res.result.account_type == 1) {
                                                switch (response.result.card_auth_status) {
                                                    case "RZZ":
                                                        that.fivetext = "1";
                                                        that.btnDisabled = true;
                                                        break;
                                                    case "RZCG":
                                                        that.fivetext = "24";
                                                        that.btnDisabled = false;
                                                        break;
                                                    case "RZSB":
                                                        that.fivetext = "";
                                                        that.rzStatus = "rzsb";
                                                        that.bindCard = false;
                                                        that.btnDisabled = false;
                                                        break;
                                                }
                                            }else {
                                                that.btnDisabled = false;
                                            }
                                        } else {
                                            that.btnDisabled = false;
                                            //
                                        }
                                    }
                                });
                            } else {
                                that.txtStatus = "";
                            }
                        } else {
                            that.$vux.alert.show(res.message);
                        }
                    }
                });
            })
        },
        methods: {
            btnClick(){
                if (this.bindCard) {
                    utils.go("/tx/withdrawals", this.$router);
                } else {
                    if (this.accountInfo.account_type == 0) {
                        //
                        utils.go("/tx/bindCard?type=1&name=" + this.accountInfo.user_name + "&num=" + this.accountInfo.certificate_number, this.$router);
                    } else {
                        //
                        utils.go("/tx/bindCard?type=2&rzStatus=" + this.rzStatus + "&company=" + this.accountInfo.company_name + "&buslince=" + this.accountInfo.buslince, this.$router);
                    }
                }


            }
        },
        components: {
            "cTopBack": require('../../components/x-top-back/x-top-back.vue'),
            "rGroup": require("../../components/group/group.vue"),
            "rButton": require("../../components/button/button.vue"),
            "rCell": require("../../components/cell/cell.vue"),
            "r-messages": require("../../components/x-messages/x-messages.vue"),
            "r-icon": require("../../components/icon/icon.vue"),
        }
    }

</script>
<style lang="less">
    @import "../../assets/styles/views/personal";
</style>


